@charset "UTF-8";
:root { --polaris-version-number: '12.21.0'; }

html, body { font-size: var(--p-font-size-325); line-height: var(--p-font-line-height-500); font-weight: var(--p-font-weight-regular); font-feature-settings: 'calt' 0; letter-spacing: initial; color: var(--p-color-text); }

html, body, button { font-family: var(--p-font-family-sans); }

html { position: relative; font-size: 100%; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-text-size-adjust: 100%; text-size-adjust: 100%; text-rendering: optimizeLegibility; scrollbar-width: thin; scrollbar-color: var(--p-color-bg) var(--p-color-bg); transition: scrollbar-color var(--p-motion-duration-100) var(--p-motion-ease-in); }

html::-webkit-scrollbar { width: 0.6875rem; background-color: var(--p-color-bg); }

html::-webkit-scrollbar-thumb { background-color: var(--p-color-bg); border: var(--p-border-width-050) solid transparent; border-radius: var(--p-border-radius-300); background-clip: content-box; }

html:hover { scrollbar-color: var(--p-color-scrollbar-thumb-bg-hover) var(--p-color-bg); }

html:hover::-webkit-scrollbar-thumb { background-color: var(--p-color-scrollbar-thumb-bg-hover); }

/* To support dynamic type in iOS, we need to set Apple's system font and then define font-families and rem-based font-sizes on descendant elements: */
@supports (font: -apple-system-body) { @media (max-width: 30.6225em) { html { font: -apple-system-body; } } }

body { min-height: 100%; margin: 0; padding: 0; background-color: #f1f2f4; scrollbar-color: var(--p-color-scrollbar-thumb-bg-hover) transparent; }

@media print { body { background-color: transparent !important; } }

*, *::before, *::after { box-sizing: border-box; }

h1, h2, h3, h4, h5, h6, p { margin: 0; font-size: 1em; font-weight: var(--p-font-weight-regular); }

button::-moz-focus-inner, [type='button']::-moz-focus-inner, [type='reset']::-moz-focus-inner, [type='submit']::-moz-focus-inner { border-style: none; }

html[class~='Polaris-Safari-16-Font-Optical-Sizing-Patch'] { font-variation-settings: 'opsz' 14; }


